.root {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background: var(--modals-plugin-overlay-bg-color);
  display: flex;
  justify-content: center;
  align-items: center; }

.modal {
  max-width: 20rem;
  max-height: 30rem;
  min-width: 20rem;
  background: var(--modals-plugin-modal-bg-color);
  border-radius: 0.25rem; }

.absoluteModal {
  position: absolute;
  left: 50%;
  transform: translateX(-50%); }

.header {
  position: relative;
  height: 2.5rem;
  padding-left: 1rem;
  padding-right: 1rem;
  background: var(--modals-plugin-modal-header-bg-color);
  border-radius: 0.25rem 0.25rem 0 0;
  display: flex;
  justify-content: center;
  align-items: center; }

.title {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  text-align: center; }

.titleText {
  font-size: 1.125rem;
  color: var(--modals-plugin-title-text-color); }

.closeButton {
  position: absolute;
  right: 0.25rem;
  top: 0.25rem;
  border-radius: 50%;
  width: 1.5rem;
  height: 1.5rem;
  display: inline-block;
  border: none;
  background: none;
  cursor: pointer;
  padding: 0;
  margin: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  background: var(--modals-plugin-close-bg); }
  .closeButton:hover {
    background: var(--modals-plugin-close-bg-hover); }
  .closeButton:active {
    background: var(--modals-plugin-close-bg-active); }

.closeIcon {
  pointer-events: none;
  width: 0.9rem;
  height: 0.9rem;
  fill: var(--modals-plugin-close-color);
  stroke: var(--modals-plugin-close-color); }

.actionLine {
  display: flex;
  justify-content: center;
  margin-bottom: 0.5rem; }

.content {
  padding: 1.5rem 1rem; }

.actionButton {
  border: none;
  background: none;
  display: inline-block;
  text-align: center;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  padding: 0.25rem 0.5rem;
  min-width: 5rem;
  border-radius: 0.125rem;
  margin: 0 0.5rem; }

.submitActionButton {
  background: var(--modals-plugin-button-active-bg);
  color: var(--modals-plugin-button-active-color); }
  .submitActionButton:hover {
    background: var(--modals-plugin-button-active-bg-hover); }

.generalActionButton {
  color: var(--modals-plugin-button-general-color); }
  .generalActionButton:hover {
    background: var(--modals-plugin-button-general-bg-hover); }

.actionButton:active {
  background: var(--modals-plugin-button-bg-active); }
